import React from 'react';
import { styled, useTheme } from '@mui/material/styles';

import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';

//icons
import AssignmentRoundedIcon from '@mui/icons-material/AssignmentRounded';
import AddCircleOutlineRoundedIcon from '@mui/icons-material/AddCircleOutlineRounded';

export default function GeneralTab() {

    const myStyle = {
        display:"flex",
        height: "100%",
        minWidth: "20%",
        flexDirection:'column',
        padding:'1rem',

    }
    
    const myStyle2 ={
        display:"flex",
        height: "100%",
        minWidth: "55%",
        flexDirection:'column',
        padding:'1rem'
    }

    return(
        <Box sx={{width:"100%", height:"100%", display:"flex"}}>
                            <Box sx={myStyle}>
                                <Typography sx={{textAlign:'center',color:'white'}} variant='h6'>Tipo de evento</Typography>
                                <FormControl>
                                    <RadioGroup
                                        aria-labelledby="demo-radio-buttons-group-label"
                                        defaultValue="publico"
                                        name="radio-buttons-group"
                                    >
                                        <FormControlLabel value="publico" control={<Radio sx={{
                                            color: "white",
                                            '&.Mui-checked': {
                                                color: "white",
                                            },
                                            }}/>} label="Público" sx={{color:"white"}}/>
                                        <FormControlLabel value="privado" control={<Radio sx={{
                                            color: "white",
                                            '&.Mui-checked': {
                                                color: "white",
                                            },
                                            }}/>} label="Privado" sx={{color:"white"}}/>
                                        
                                    </RadioGroup>
                                </FormControl>
                            </Box>
                            <Divider orientation="vertical" variant="middle" flexItem />
                            <Box sx={myStyle}>
                                <Typography sx={{textAlign:'center',color:'white'}} variant='h6'>Invitados</Typography>
                                
                                <Box sx={{display:"flex", marginTop:"0.5rem"}}>
                                    <AssignmentRoundedIcon sx={{ mr: 1 , color:"white"}} />
                                    <Typography sx={{color:'white'}}>Ver lista de invitados</Typography>
                                </Box>

                                <Box sx={{display:"flex", marginTop:"0.5rem"}}>
                                    <AddCircleOutlineRoundedIcon sx={{ mr: 1 , color:"white"}} />
                                    <Typography sx={{color:'white'}}>Agregar invitado</Typography>
                                </Box>
                                
                                
                            </Box>
                            <Divider orientation="vertical" variant="middle" flexItem />
                            <Box sx={myStyle2}>
                                <Typography sx={{color:'white'}} variant='h6'>Información</Typography>
                                <Box sx={{display:"flex", marginTop:"0.5rem", justifyContent:'space-between' ,width:"100%"}}>
                                    <TextField id="filled-basic" label="Descripción del evento..." color="primary" variant="filled" />
                                    <Box sx={{display:'flex', alignContent:'flex-end'}}>
                                        <Typography sx={{color:'white'}}>Agregar colaborador</Typography>
                                        <AddCircleOutlineRoundedIcon sx={{ mr: 1 , color:"white"}} />
                                    </Box>
                                </Box>
                                
                            </Box>
                        </Box>
    );
}