import React from 'react';



import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import CustomCard from '../../Cards/CustomCard';

export default function ConfigEstanterias() {
   

    const pestanaStyle={
        width:"7rem",
        height:"100%",
        backgroundColor:"lightGray",
        marginRight:"0.5rem",
        borderRadius: "0px 0px 5px 5px" 
    }

    
    return(
        <Box sx={{width:"100%", height:"100%", display:"flex"}}>
                        <Box sx={{width:'15%', height:'100%', display:'flex', alignItems:'center', justifyContent:'center'}}>
                            <Typography sx={{textAlign:'center',color:'white'}} variant='h6'>Configuración estantes</Typography>
                        </Box>
                        <Divider orientation="vertical" variant="middle" flexItem />
    
                        {/* Dividir en componentes */}
                        <Box sx={{width:'85%', height:'100%', display:'flex', alignItems:'center', justifyContent:'center'}}>
                        
                            <Box sx={{width:'90%', height:'80%',backgroundColor: '#676767', display:'flex'}}>
    
                                {/* Pestañas superiores */}
                                <Box sx={{width:"auto",height:"1.8rem", position:"absolute", top:"0", display:"flex"}}>
                                        <Box id="tab-config" className="o-tab-active" sx={pestanaStyle}><Button sx={{padding:0}} variant="text"  id="btn-config">Configuración</Button></Box>
                                </Box>
    
                               <Box sx={{display:"flex", alignItems:"flex-end",padding:"1rem"}}>
                                    <CustomCard size="sm" number="1" type={1} image="Prev_Estante_1.jpg"></CustomCard>
                                    <CustomCard size="sm" number="2" type={1} image="Prev_Estante_1.jpg"></CustomCard>
                                    <CustomCard size="sm" number="3" type={1} image="Prev_Estante_3.jpg"></CustomCard>
                                    <CustomCard size="sm" number="4" type={1} image="Prev_Estante_2.jpg"></CustomCard>
                                    <CustomCard size="sm" number="5" type={1} image="Prev_Estante_1.jpg"></CustomCard>
                               </Box>
                                   
                                
                            </Box>
                        </Box>
                    </Box>
    )
}